export default "\r\n/* 🔹 Container for MS Description */\r\n.ms-description-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  background: #ffffff;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n  width: 100%;\r\n  border: 1px solid #ccc;\r\n}\r\n/* 🔹 Flexbox for proper alignment */\r\n.ms-description-content {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  background: #f6f6f6;\r\n  padding: 10px;\r\n  border-radius: 4px;\r\n}\r\n/* 🔹 Proper text alignment */\r\n.description-text {\r\n  font-size: 16px;\r\n  color: #333;\r\n}\r\n/* 🔹 Edit button */\r\n.edit-btn-container {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n.edit-btn {\r\n  color: #184569;\r\n  font-size: 18px;\r\n  padding: 5px;\r\n  cursor: pointer;\r\n  transition: 0.2s;\r\n}\r\n.edit-btn:hover {\r\n  color: #2980b9;\r\n}\r\n/* 🔹 Edit box (Textarea) */\r\n.edit-box {\r\n  margin-top: 10px;\r\n}\r\n/* 🔹 Mobile Responsiveness */\r\n@media screen and (max-width: 768px) {\r\n  .ms-description-content {\r\n      flex-direction: column;\r\n      align-items: flex-start;\r\n  }\r\n  .edit-btn-container {\r\n      margin-top: 5px;\r\n      text-align: right;\r\n      width: 100%;\r\n  }\r\n}\r\n/* Fix accordion spacing & alignment */\r\n.view-name-container {\r\n  display: flex;\r\n  align-items: center;\r\n  background: #184569;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n  width: 100%;\r\n}\r\n/* Ensures the header text is properly aligned */\r\n.view-title {\r\n  display: flex;\r\n  align-items: center;\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n  color: #ffffff;\r\n  margin: 0;\r\n  width: 100%;\r\n}\r\n/* Properly align icon with text */\r\n.view-icon {\r\n  width: 25px;\r\n  height: auto;\r\n  margin-right: 10px;\r\n  vertical-align: middle;\r\n}\r\n/* General table styling */\r\n.table {\r\n  width: 100%;\r\n  display: table;\r\n  border-collapse: collapse;\r\n  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);\r\n}\r\n/* Table row structure */\r\n.row {\r\n  display: table-row;\r\n  background: #f6f6f6;\r\n}\r\n/* Alternate row colors */\r\n.row:nth-of-type(odd) {\r\n    background: #e9e9e9;\r\n  }\r\n/* Header row styling */\r\n.row.header {\r\n    font-weight: bold;\r\n    color: #ffffff;\r\n    background: #184569;\r\n  }\r\n/* Table cell styling */\r\n.cell {\r\n  display: table-cell;\r\n  padding: 10px;\r\n  text-align: left;\r\n}\r\n/* Mobile Responsive Adjustments */\r\n@media screen and (max-width: 768px) {\r\n  .view-title {\r\n    font-size: 16px;\r\n  }\r\n\r\n  .table {\r\n    display: block;\r\n  }\r\n\r\n  .row {\r\n    display: block;\r\n    margin-bottom: 10px;\r\n  }\r\n\r\n  .cell {\r\n    display: block;\r\n    text-align: left;\r\n    padding: 5px;\r\n  }\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"